@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
@import url(/reset-css.css);
.app{background-color:#121212;width:100%;height:100%}

.auth{display:flex;align-items:center;justify-content:center;width:100%;height:100%}.auth-card{display:grid;grid-row-gap:1rem;justify-items:center;padding:1rem;background-color:#282828;border-radius:.3rem}.auth-card__title{color:#fff;font:700 2rem/2.55rem "Lato",sans-serif}.auth-card__btn{width:-webkit-min-content;width:-moz-min-content;width:min-content;padding:.8rem 1.5rem .8rem;background-color:#1db954;border:none;border-radius:.3rem;color:#121212;font:700 1rem/1 "Lato",sans-serif;text-transform:uppercase}

.loading-spinner{color:#b3b3b3;font-size:2rem}

.callback{display:flex;align-items:center;justify-content:center;width:100%;height:100%}

.client{display:grid;grid-template-areas:"sidebar header" "sidebar content" "player player";grid-template-rows:60px 1fr 90px;grid-template-columns:200px 1fr;width:100%;height:100%}.client__content{grid-area:content;overflow-x:hidden;overflow-y:scroll;scroll-behavior:smooth;width:100%;height:100%}.client__content::-webkit-scrollbar{width:10px}.client__content::-webkit-scrollbar-thumb{background:#282828;border-radius:32px}

.subpage-content{width:100%;height:auto;padding:.8rem}

.error-card{width:-webkit-min-content;width:-moz-min-content;width:min-content;min-width:250px;max-width:100%;display:grid;grid-template-areas:"title status" "message message";justify-content:space-between;grid-gap:1rem;gap:1rem;padding:.8rem;border:2px solid #de6868;border-radius:.3rem;background-color:#db4b4b}.error-card__title{grid-area:title;font:700 1.4rem/1.6rem "Lato",sans-serif;text-transform:uppercase}.error-card__status{grid-area:status;font:400 1.25rem/1.4rem "Lato",sans-serif}.error-card__msg{grid-area:message;font:400 1.15rem/1.3rem "Lato",sans-serif}

.set-status{width:100%;display:flex}.set-status--loading{justify-content:center;padding:.8rem}.set-status--error{justify-content:left}.set-status--hide{display:none}

.items-set-content{width:100%;height:auto}.items-set-content--list{display:flex;flex-direction:column}.items-set-content--grid{display:grid;grid-gap:0.8rem;grid-template-columns:repeat(auto-fill, minmax(164px, 1fr))}

.items-set-header{display:flex;flex-direction:row;align-items:center;justify-content:space-between;padding:.8rem 0}.items-set-header__title{color:#fff;font:700 1.7rem/2.2rem "Lato",sans-serif}.items-set-header__more-url{color:#b3b3b3;font:400 1.15rem/1.3rem "Lato",sans-serif;text-transform:uppercase;text-decoration:none;cursor:pointer}

.track-item{display:grid;grid-template-areas:"control cover description duration";grid-template-rows:1fr;grid-template-columns:auto 50px 1fr auto;grid-gap:.8rem;gap:.8rem;align-items:center;padding:.1rem .8rem;margin:.1rem 0;border-radius:.3rem}.track-item--no-cover{grid-template-areas:"control description duration";grid-template-columns:auto 1fr auto}.track-item:hover{background-color:#282828}

.cover{grid-area:cover;position:relative}.cover:after{content:"";display:block;padding-bottom:100%}.cover__content{position:absolute;width:100%;height:100%;overflow:hidden}.cover__img{width:100%;height:auto}

.track-control{grid-area:control}.track-control__icon{color:#fff;font-size:1.5rem}.track-control--active .track-control__icon{color:#1ed760}

.track-description{grid-area:description;display:grid;grid-template-columns:1fr;grid-template-rows:repeat(2, 1fr)}.track-description__title{color:#fff;font:700 1.1rem/1.45rem "Lato",sans-serif;display:inline-block;max-width:100%;overflow:hidden;white-space:nowrap;text-overflow:ellipsis}.track-description__artists{color:#b3b3b3;font:400 1.1rem/1.45rem "Lato",sans-serif;display:inline-block;max-width:100%;overflow:hidden;white-space:nowrap;text-overflow:ellipsis}.track-description--active .track-description__title{color:#1ed760}

.track-duration{grid-area:duration;color:#b3b3b3;font:400 1.1rem/1.45rem "Lato",sans-serif;display:inline-block;max-width:100%;overflow:hidden;white-space:nowrap;text-overflow:ellipsis}

.header-type{grid-area:type;color:#b3b3b3;font:400 1rem/1.3rem "Lato",sans-serif;text-transform:uppercase}

.header-title{grid-area:title;color:#fff;font:600 2.5rem/3rem "Lato",sans-serif;display:inline-block;max-width:100%;overflow:hidden;white-space:nowrap;text-overflow:ellipsis}

.header-detail{grid-area:details;display:flex;align-items:center;color:#b3b3b3;font:400 1.15rem/1.3rem "Lato",sans-serif}.header-detail svg{color:#1ed760;margin-right:1rem}

.media-header{width:100%;display:grid;grid-template-areas:"cover ." "cover type" "cover title" "cover details";grid-template-columns:200px 1fr;grid-template-rows:1fr repeat(3, -webkit-min-content);grid-template-rows:1fr repeat(3, min-content);align-items:flex-end;grid-gap:0.8rem;gap:0.8rem;padding:1rem 1rem 0}.media-header--loading{padding:1rem;display:flex;align-items:center;justify-content:center}

.link{padding:.8rem 1.5rem .8rem;color:#b3b3b3;font:700 1rem/1 "Lato",sans-serif;text-decoration:none}.link.active{background-color:#282828;border-radius:.3rem;color:#fff}

.navigation{width:100%;height:-webkit-min-content;height:-moz-min-content;height:min-content;display:flex}.navigation--row{width:auto;flex-direction:row}.navigation--column{flex-direction:column}

.card-header{position:relative}.card-header__icon-wrapper{position:absolute;top:0;right:0;bottom:0;left:0;display:flex;align-items:center;justify-content:center;z-index:2;opacity:0;background-color:rgba(40,40,40,0.5);transition:.2s}.card-header__icon-wrapper:hover{opacity:1}.card-header__icon{color:#1ed760;font-size:4rem}

.card-content{padding:.8rem}

.card-title{color:#fff;font:700 1.3rem/1.4rem "Lato",sans-serif;text-decoration:none;display:inline-block;max-width:100%;overflow:hidden;white-space:nowrap;text-overflow:ellipsis}.card-title--active{color:#1ed760}

.card-type{color:#b3b3b3;font:400 0.8rem/0.9rem "Lato",sans-serif;text-transform:uppercase}

.card-description{margin-top:.7rem;color:#fff;font:400 1.2rem/1.3rem "Lato",sans-serif;text-decoration:none;display:inline-block;max-width:100%;overflow:hidden;white-space:nowrap;text-overflow:ellipsis}

.media-card{width:100%;max-width:100%;overflow:hidden;border-radius:.3rem;background-color:#282828}

.client-header{grid-area:header;display:flex;align-items:center;justify-content:space-between;padding:.8rem .8rem .8rem 0;background-color:#040404;width:100%;height:100%}.log-out-wrapper{width:100%;display:flex;align-items:center;justify-content:flex-end}.log-out-wrapper__btn{padding:.8rem 1.5rem .8rem;outline:none;background-color:transparent;border:2px solid #db4b4b;border-radius:.3rem;color:#db4b4b;font:700 1rem/1 "Lato",sans-serif;text-transform:uppercase}

.client-sidebar{grid-area:sidebar;display:grid;padding:1rem;background-color:#040404;width:100%;height:100%}

.music-control{grid-area:music-control;display:grid;grid-template-columns:1fr;grid-template-rows:repeat(2, 1fr)}.action-buttons{display:grid;justify-content:center;align-items:center;grid-gap:2.5rem;gap:2.5rem;grid-template-columns:repeat(3, -webkit-min-content);grid-template-columns:repeat(3, min-content);grid-template-rows:1fr}.action-buttons__main-icon{color:#b3b3b3;font-size:2.3rem;transition:.2s}.action-buttons__main-icon:hover{color:#fff;transform:scale(1.05)}.action-buttons__aside-icon{color:#b3b3b3;font-size:1.3rem;transition:.2s}.action-buttons__aside-icon:hover{color:#fff;transform:scale(1.05)}.progress-bar-wrapper{display:grid;justify-content:center;align-items:center;grid-template-columns:auto 1fr auto;grid-template-rows:1fr;grid-gap:.8rem;gap:.8rem}.progress-bar-wrapper__duration{color:#b3b3b3;font:700 0.9rem/1rem "Lato",sans-serif}.progress-bar-wrapper__bar{-webkit-appearance:none;background:transparent;outline:none}.progress-bar-wrapper__bar::-webkit-slider-thumb{-webkit-appearance:none}.progress-bar-wrapper__bar::-ms-track{width:100%;cursor:pointer;background:transparent;border-color:transparent;color:transparent}.progress-bar-wrapper__bar::-webkit-slider-thumb{width:13px;height:13px;border-radius:32px;background-color:#fff;border-radius:1px solid #040404;margin-top:-4px}.progress-bar-wrapper__bar::-moz-range-thumb{width:13px;height:13px;border-radius:32px;background-color:#fff;border-radius:1px solid #040404}.progress-bar-wrapper__bar::-ms-thumb{width:13px;height:13px;border-radius:32px;background-color:#fff;border-radius:1px solid #040404}.progress-bar-wrapper__bar::-webkit-slider-runnable-track{width:100%;height:4px;cursor:pointer;background:#505050;border-radius:32px}.progress-bar-wrapper__bar::-ms-track{width:100%;height:4px;cursor:pointer;background:#505050;border-radius:32px}.progress-bar-wrapper__bar::-moz-range-track{width:100%;height:4px;cursor:pointer;background:#505050;border-radius:32px}.progress-bar-wrapper__bar::-moz-range-progress{background-color:#1ed760}.progress-bar-wrapper__bar::-ms-fill-lower{background-color:#1ed760}

.track-info{grid-area:track-info;display:flex;flex-direction:row;width:100%;height:100%}.track-info__cover{height:100%;max-height:100%;margin-right:.8rem}.track-details{display:grid;grid-template-areas:"name" "artists";grid-gap:.5rem;gap:.5rem;width:100%;height:100%}.track-details__name{grid-area:name;align-self:flex-end;color:#fff;font:700 1.3rem/1.65rem "Lato",sans-serif;display:inline-block;max-width:100%;overflow:hidden;white-space:nowrap;text-overflow:ellipsis}.track-details__artists{grid-area:artists;align-self:flex-start;color:#b3b3b3;font:400 1rem/1.3rem "Lato",sans-serif;display:inline-block;max-width:100%;overflow:hidden;white-space:nowrap;text-overflow:ellipsis}

.player{grid-area:player;padding:.8rem;display:grid;grid-template-columns:1fr 2fr 1fr;grid-template-rows:1fr;grid-template-areas:"track-info music-control .";background-color:#282828;width:100%;height:100%}.player--loading{display:flex;align-items:center;justify-content:center}

html,body,#root{height:100%;width:100%}:root{font-size:14px}*{margin:0;box-sizing:border-box}

